
html {
  color: #000;
  background: #fff;
  overflow-y: scroll;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
html * {
  outline: 0;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: transparent;
}
body,
html {
  font-family: sans-serif;
}
article,
aside,
blockquote,
body,
button,
code,
dd,
details,
div,
dl,
dt,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
hr,
input,
legend,
li,
menu,
nav,
ol,
p,
pre,
section,
td,
textarea,
th,
ul {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
input,
select,
textarea {
  font-size: 100%;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
del {
  text-decoration: line-through;
}
address,
caption,
cite,
code,
dfn,
em,
th,
var {
  font-style: normal;
  font-weight: 500;
}
ol,
ul,
li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: 500;
}
q:after,
q:before {
  content: '';
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -.5em;
}
sub {
  bottom: -.25em;
}
a:hover {
  text-decoration: none;
}
a,
ins {
  text-decoration: none;
}
button,
input[type=button],
input[type=file],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: none;
}
body {
  font-size: 12px;
  height: 100%;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.icon-wjx {
  color: #ffac2d;
}
.icon-wjx-copy {
  color: #ccc;
}
.loading {
  width: 100px;
  height: 100px;
  background: #1ABC9C;
  text-align: center;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 50%;
  z-index: 99999;
}
.loading img {
    margin-top: 5px;
}
.icon-chenggong {
  color: #1ABC9C;
}
.icon-shibai {
  color: #fa5454;
}
.starList {
  height: 15px;
  width: 75px;
  background: url(assets/star.png);
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}



